* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
}

.coutry-item {
  display: flex;
}

.country-flag {
  margin-right: 10px;
}
.card-name {
  display: flex;
}
.card-flag {
  margin-right: 10px;
}

.card-titles {
  font-weight: bold;
}

.card-capital,
.card-popilation,
.card-languages,
.card-name,
.card-titles {
  font-size: 23px;
}
